import React, {useEffect, useState} from "react";
import {Button, Cell, Footer, Panel, PanelHeader, PanelHeaderBack, Placeholder} from "@vkontakte/vkui";
import Icon56ArticleOutline from "@vkontakte/icons/dist/56/article_outline";

const AllOrderds = ({id, params, goBack}) => {
    const [allOrders, setAllOrders] = useState(null);
    useEffect(()=>{
        fetch("https://sahpossum.herokuapp.com/pay/getAllOrders", {
            method: "POST", // *GET, POST, PUT, DELETE, etc.
            mode: "cors", // no-cors, cors, *same-origin
            cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
            credentials: "same-origin", // include, *same-origin, omit
            headers: { "Content-Type": "application/json" },
            redirect: "follow", // manual, *follow, error
            referrer: "no-referrer", // no-referrer, *client
            body: JSON.stringify({params: params}),
        })
            .then(res=>res.json())
            .then(res=>{
                console.log(res)
                if (!res.error) setAllOrders(res)
            })
    }, [])
    if (allOrders === null) return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goBack} />} />
            <Placeholder
                stretched
                icon={<Icon56ArticleOutline />}
                header={"Загрузка"}
            />
            <Footer />
        </Panel>
    )
    if (allOrders.length === 0) return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goBack} />} />
            <Placeholder
                stretched
                icon={<Icon56ArticleOutline />}
                header={"У вас нет текущий доставок"}
                action={
                    <Button onClick={goBack} mode={"outline"} size="l">
                        Продолжить покупки
                    </Button>
                }
            />
            <Footer />
        </Panel>
    )
    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goBack} />} />
            {
                allOrders.map(order=> {
                    return (
                        <Cell
                            multiline
                            indicator={order.status}
                            description={order.track ? 'Трек-номер '+order.track : 'Ожидание отправки'}
                        >{`Заказ №${order.num} (${order.sum}руб.)`}</Cell>
                    )
                })
            }
            <Footer />
        </Panel>
    )
}
export default AllOrderds